@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbarContainer{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding:10px;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
}

.display-bar
{
    display: none;
}

.navbarContainer ul{
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
}


.navbarContainer ul li{
    float: left;
    cursor: pointer;
    border-right: 1px solid #eee;
    padding: 25px;
    color: #000;
    transition: 0.1s ease-in;
}

.navbarContainer ul li:first-child{
    border-left: 1px solid #eee;
    transition: 0.1s ease-out;
}

.navbarContainer ul li:hover{
    color: coral !important;
}


@media screen and (max-width: 600px) {
    .navbarContainer ul{
        flex-direction: column;
        display: none;
    }

    .navbarContainer{
        flex-direction: column;
        padding: 0;
    }

    .navbarContainer ul li{
        text-align: center;
        border: 0;
    }
    .navbarContainer ul li:first-child{
        border: 0;
    }
    .display-bar{
        display: flex;
        font-size: 25px;
        padding: 25px;
    }
}
.headerContainer{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 10px;
}

.headerGroup{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (max-width: 600px) {
    .headerGroup{
        display: none;
    }

    .headerContainer{
        justify-content: center;
        padding: 0;
    }
}
.sliderContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #eee;
    height:380px;
}
.separator{
    width:100%;
    height: 5px;
    background-color: #eee;
    display: flex;
    align-items: center;
    justify-content: center;
}

.separatorHead{
    text-align: center;
    padding: 15px;
    font-size: 25px;
    background-color: white;
}
.container{
    left: 0;
    bottom: 0;
    display: flex;
    border-top: 1px solid #eee;
    justify-content: space-between;
    padding: 25px;
    background-color: #eee;
}

.footer-company p{
    font-weight: normal;
    font-size: 14px;
}

.footer-social-media-list{
    font-size: 30px;
    color: grey;
    font-weight: normal;
    display: flex;
    align-items: center;
}

.footer-fast ul li{
    transition: 100ms ease-in-out;
    cursor: pointer;
    font-size: 14px;
}

.footer-fast ul li:hover{
    color: crimson;
}

@media screen and (max-width: 600px) {
    .container{
        flex-direction: column;
    }
}
.cardContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    margin-right: 8px;
    width: 220px;
}

.cardContainer:last-child{
    padding: 0;
}

.cardButton{
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    background-color: #eee;
    border-radius: 100px;
    height:30px;
    font-weight: bold;
    font-size: 13px;
    cursor: pointer;
    transition: 0.1s ease-in-out;    
}

a{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content:center ;
}

.cardButton:hover{
    background-color: coral;
    color: white;
box-shadow: -5px 0px 31px -5px rgb(252, 185, 161);
}

.cardPic{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 220px;
}

.cardPic{
    border-radius: 2px;
}

.cardHead{
    font-weight: bold;
}

.cardPrice{
    font-weight: regular;
}

.cardBody{
    width:100%;
    padding:15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 15px;
    text-align: center;
    justify-content: center;
}
body{
    font-family: 'Poppins', sans-serif;
}

.Content{
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding:55px;
    padding-top: 15px;
}

.productContainer{
    padding:8px;
    margin-top: 15px;
}
.productContainer .item{
    display: flex;
    justify-content: center;
    align-items: center;
}

.separatorContainer{
    margin-top: 35px;
    margin-bottom: 35px;
}

@media screen and (max-width: 600px) {
    .productContainer{
        flex-direction: column;
    }
}
.productDetailContent
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 35px;
    padding-bottom: 0;
}

.productDetailContainer{
    display: flex;
    margin-bottom: 50px;
}

.productDescription{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 15px;
    border: 1px solid #eee;
    flex: 2 1;
}

.productDetailPhoto{
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1 1;
    padding: 15px;
    border: 1px solid #eee;
    margin-right: 5px;
}

.productDetailPhoto img{
    height: 380px;
}

.bold{
    font-weight: bold;
}

.tel{
    font-size: 32px;
}

.suggestContainer{
    padding:8px;
}

.suggestContainer .item{
    display: flex;
    justify-content: center;
    align-items: center;
}

.productDetailSeparator{
    margin-bottom: 25px;
}

@media screen and (max-width: 600px) {
    .productDetailContainer{
        flex-direction: column;
        padding: 0;
    }

    .productDetailPhoto img{
        height: 250px;
    }

    .productDetailPhoto{
        margin-right: 0;
        margin-bottom: 5px;
    }

    .tel{
        font-size: 25px;
    }
}
